
.item-wrap {
  margin: auto;
  margin-top: 10px;
  position: relative;
}
.item-img {
  background: rgba(0, 0, 0, 0.6);
  border-radius: 4px;
  width: 600px;
  height: 300px;
  overflow: hidden;
}
.item-img img {
  width: 100%;
  height: 100%;
}
.item-title {
  position: absolute;
  left: 0;
  bottom: 20px;
  width: 100%;
  text-align: center;
  color: #fff;
}
.item-title .n1 {
  line-height: 40px;
  font-size: 28px;
}
.item-title .n2 {
  line-height: 28px;
  font-size: 20px;
  margin-top: 10px;
}



