
.page-loading {
  position: fixed;
  z-index: 99999999;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #fff;
}
.page-loading-img {
  position: absolute;
  z-index: 9999;
  top: 50%;
  left: 50%;
  margin-left: -40px;
  margin-top: -40px;
  width: 80px;
  height: 80px;
}



